body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#App {
  background-color: hsl(240, 50%, 10%);
  height: 100vh;
  color: white;
}
#Scene {
  width: 250px;
  height: 250px;
  position: relative;
  top: calc(100vh / 2 - 125px);
  left: calc(100vw / 2 - 125px);
  -webkit-perspective: 500px;
          perspective: 500px;
}
#Cube {
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.Face {
  position: absolute;
  background-image: linear-gradient(
    hsla(270, 100%, 20%, 0.05),
    95%,
    hsla(270, 100%, 50%, 0.2)
  );
}
.Face#front {
  -webkit-transform: rotateY(0deg) translateZ(125px);
          transform: rotateY(0deg) translateZ(125px);
}
.Face#left {
  -webkit-transform: rotateY(-90deg) translateZ(125px);
          transform: rotateY(-90deg) translateZ(125px);
}
.Face#right {
  -webkit-transform: rotateY(90deg) translateZ(125px);
          transform: rotateY(90deg) translateZ(125px);
}
.Face#back {
  -webkit-transform: rotateY(180deg) translateZ(125px);
          transform: rotateY(180deg) translateZ(125px);
}
.Face#top {
  -webkit-transform: rotateX(90deg) translateZ(125px);
          transform: rotateX(90deg) translateZ(125px);
}
.Face#bottom {
  -webkit-transform: rotateX(-90deg) translateZ(125px);
          transform: rotateX(-90deg) translateZ(125px);
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
  }
}
