svg {
}

circle,
rect {
  fill: transparent;
}
line,
circle,
rect {
  stroke: hsla(270, 100%, 50%, 0.25);
}
text {
  stroke: white;
}
circle {
  stroke-width: 0;
  fill: hsl(90, 100%, 50%);
}

